@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

h1,h2,h3,h4,h5,h6{font-family:"Roboto",Arial,Helvetica,sans-serif}h1,.h1-like{font-size:5.5rem}@media(max-width: 768px){h1,.h1-like{font-size:5rem}}h2,.h2-like{font-size:4.5rem}@media(max-width: 768px){h2,.h2-like{font-size:4rem}}h3,.h3-like{font-size:3.5rem}@media(max-width: 768px){h3,.h3-like{font-size:3rem}}h4,.h4-like{font-size:2.5rem}@media(max-width: 768px){h4,.h4-like{font-size:2rem}}html{font-size:62.5%}.card-grid{padding:1.25rem;max-width:140rem;margin:0 auto;display:flex;flex-wrap:wrap;justify-content:center;align-items:center}.card-grid.easy{max-width:100rem}@media screen and (max-width: 1012px){.card-grid.easy{max-width:100%}}@media screen and (max-width: 768px){.card-grid.easy .card{max-width:14rem}}@media screen and (max-width: 544px){.card-grid.easy .card{max-width:10rem}}.card-grid.medium{max-width:100rem}@media screen and (max-width: 1012px){.card-grid.medium{max-width:100%}}@media screen and (max-width: 768px){.card-grid.medium .card{max-width:12rem}}@media screen and (max-width: 544px){.card-grid.medium .card{max-width:10rem}}.card-grid.hard{max-width:140rem}@media screen and (max-width: 1012px){.card-grid.hard{max-width:100%}}@media screen and (max-width: 768px){.card-grid.hard .card{max-width:12rem}}@media screen and (max-width: 544px){.card-grid.hard .card{max-width:8rem}}button{background:none;border:2px solid #fff;padding:.375rem .75rem;border-radius:4px;color:#fff;font-weight:700;cursor:pointer;font-size:1.8rem}button:hover{background:#c23866;color:#fff}.header{display:flex;flex-direction:column;justify-content:space-between;align-items:center;max-width:100rem;margin:0 auto;padding:1rem}.header .logo-wrapper{display:flex;width:100%;justify-content:space-between;margin-bottom:2rem}.header .logo{margin:0}.header .reset-game{fill:#fff;width:5rem;border:0}.header .difficulty-list{list-style-type:none;padding:0;display:flex;justify-content:center;margin:0}.header .difficulty-item{margin:0 1rem}.card{position:relative;margin:1rem;cursor:pointer;max-width:18rem}.card.flipped .front{-webkit-transform:rotateY(0deg);transform:rotateY(0deg);transition-delay:.2s}.card.flipped .back{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);transition-delay:0s}.card img{width:100%;height:100%;display:block;border:3px solid #fff;border-radius:6px}.card .front{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);transition:all ease-in .2s;position:absolute}.card .back{transition:all ease-in .2s;transition-delay:.2s}.card.border img{border-color:#f2ca19}
